@import "../../examples/custom-examples/colorsExample";
@import "../../examples/custom-examples/typographyExample.scss";

html,
body,
#blueprint-styler,
.app-wrapper,
.app
{
  width: 100%;
  // height: 100%;
  // scroll-behavior: smooth;
}


$app-max-width: 70rem;
$menu-width: 19rem;
$menu-buffer: 50rem;
$menu-vert-padding: 4rem;

.app-wrapper{
  background-color: $light-gray5;
  &.bp3-dark{
    background-color: $dark-gray2;
  }
}

.app{
  max-width: $app-max-width;
  margin: 0  auto;
  position: relative;
}

.styler-menu{
  position: fixed;
  top: 0;
  // left: 0;
  right: auto;
  bottom: 0;
  overflow-y: auto;
  width: $menu-width + $menu-buffer;
  margin-left: -$menu-buffer;
  padding: $menu-vert-padding 1rem 2rem ($menu-buffer + $menu-vert-padding);

  border-right: 1px solid $pt-divider-black;
  background-color: $white;
  .bp3-dark &{
    background-color: $dark-gray3;
    border-color: $pt-dark-divider-black;
  }

  display: flex;
  flex-direction: column;
  &__nav{
    flex: 1 0 auto;
  }

  &__nav{
    margin-bottom: 2rem;
  }

  &__footer{
    color: $pt-text-color-muted;
    // text-align: right;
    a:not(:hover){
      color: inherit;
      text-decoration: underline;
    }
  }

}

.styler-examples{
  padding: 1.5rem;
  margin-left: $menu-width;
}

.styler-component{
  margin: 1rem 0;
}
.styler-component-header{
  padding-top: 2rem;
}
.styler-section-header{
  margin-top: 5rem;
}

.docs-example-frame{
  margin: 1rem 0;
}

